import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { reauthenticateWithCredential, EmailAuthProvider } from 'firebase/auth';
import { auth } from '../../../config/firebaseConfig';
import {
  getProfile,
  updateProfile,
  changePassword,
  setPasswordValid,
  setPasswordError,
  setConfirmPasswordValid,
  setConfirmPasswordError,
} from './profileSlice';
import { useNavigate } from 'react-router-dom';
import {
  ProfileContainer,
  Button,
  ProfileSection,
  ProfileItem,
  Input,
  PasswordSection,
  Line,
  FormButton,
  ErrorText,
  SuccessText,
  HelperText,
} from './style';

const Profile = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const {
    profile,
    passwordValid,
    confirmPasswordValid,
    passwordError,
    confirmPasswordError,
    error,
  } = useSelector((state) => state.profile);
  const { isAuthenticated, loading } = useSelector((state) => state.login);

  // 
  const [formData, setFormData] = useState({
    name: '',
    birthday: '',
    phone: '',
    email: '',
    address: '',
    oldPassword: '',
    password: '',
    confirmPassword: '',
  });

  // 
  useEffect(() => {
    if (!loading && isAuthenticated) {
      dispatch(getProfile());
    } else if (!isAuthenticated) {
      navigate('/login');
    }
  }, [loading, isAuthenticated, dispatch, navigate]);

  //  profile  formData 
  useEffect(() => {
    if (profile) {
      setFormData((prev) => ({ ...prev, ...profile }));
    }
  }, [profile]);

  // 
  const handleChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  // 
  const handleSave = async () => {
    try {
      await dispatch(
        updateProfile({
          name: formData.name,
          birthday: formData.birthday,
          phone: formData.phone,
          address: formData.address,
        })
      ).unwrap();
      window.alert('');
    } catch (error) {
      console.error(':', error);
      window.alert('');
    }
  };

  // 
  const verifyOldPassword = async (oldPassword) => {
    const user = auth.currentUser;
    const credential = EmailAuthProvider.credential(user.email, oldPassword);

    try {
      await reauthenticateWithCredential(user, credential);
      return true;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  };

  // 
  const validatePassword = (password) => {
    let errorMessage = '';
    const re = /^[A-Z].{7,}$/; // 8
    const hasUpperCaseFirst = /^[A-Z]/.test(password);
    const isValidLength = password.length >= 8;

    if (!hasUpperCaseFirst) {
      errorMessage = '';
    } else if (!isValidLength) {
      errorMessage = '8';
    }

    const isValid = re.test(password);
    dispatch(setPasswordValid(isValid));
    dispatch(setPasswordError(isValid ? '' : errorMessage));
  };

  // 
  const validateConfirmPassword = (password, confirmPassword) => {
    const isValid = confirmPassword === password && passwordValid;
    dispatch(setConfirmPasswordValid(isValid));
    dispatch(setConfirmPasswordError(isValid ? '' : ''));
  };

  // 
  const handlePasswordChange = async () => {
    const { oldPassword, password } = formData;

    try {
      // 
      const isOldPasswordValid = await verifyOldPassword(oldPassword);
      if (!isOldPasswordValid) {
        window.alert('');
        return;
      }

      // 
      if (!passwordValid) {
        window.alert('');
        return;
      }

      // 
      if (!confirmPasswordValid) {
        window.alert('');
        return;
      }

      // 
      await dispatch(changePassword(password)).unwrap();

      // 
      window.alert('');

      // 
      await auth.signOut();
      navigate('/');
    } catch (error) {
      console.error(':', error);
      window.alert('');
    }
  };

  return (
    <ProfileContainer>
      <div>
        <Button onClick={() => navigate('/orders')}></Button>
        <Button
          onClick={() => {
            auth.signOut();
            navigate('/');
          }}
        >
          
        </Button>
        <Button onClick={() => navigate('/deleteaccount')}></Button>
      </div>
      <ProfileSection>
        <ProfileItem>
          <label>:</label>
          <Input type="text" value={formData.email} disabled />
        </ProfileItem>
        <ProfileItem>
          <label>:</label>
          <Input
            type="text"
            name="name"
            placeholder=""
            value={formData.name}
            onChange={handleChange}
          />
        </ProfileItem>
        <ProfileItem>
          <label>:</label>
          <Input
            type="date"
            name="birthday"
            value={formData.birthday}
            onChange={handleChange}
          />
        </ProfileItem>
        <ProfileItem>
          <label>:</label>
          <Input
            type="text"
            name="phone"
            value={formData.phone}
            onChange={handleChange}
          />
        </ProfileItem>
        <ProfileItem>
          <label>:</label>
          <Input
            type="text"
            name="address"
            placeholder=""
            value={formData.address}
            onChange={handleChange}
          />
        </ProfileItem>
        <FormButton onClick={handleSave}></FormButton>
      </ProfileSection>
      <Line />
      <PasswordSection>
        <ProfileItem>
          <label>:</label>
          <Input
            type="password"
            name="oldPassword"
            onChange={handleChange}
          />
        </ProfileItem>
        <ProfileItem>
          <label>:</label>
          <Input
            type="password"
            name="password"
            onChange={(e) => {
              handleChange(e);
              validatePassword(e.target.value);
            }}
          />
          {formData.password &&
            (passwordValid ? (
              <SuccessText></SuccessText>
            ) : (
              <ErrorText> {passwordError}</ErrorText>
            ))}
        </ProfileItem>
        <HelperText>8</HelperText>
        <ProfileItem>
          <label>:</label>
          <Input
            type="password"
            name="confirmPassword"
            onChange={(e) => {
              handleChange(e);
              validateConfirmPassword(formData.password, e.target.value);
            }}
          />
          {formData.confirmPassword &&
            (confirmPasswordValid ? (
              <SuccessText></SuccessText>
            ) : (
              <ErrorText> {confirmPasswordError}</ErrorText>
            ))}
        </ProfileItem>
        <FormButton onClick={handlePasswordChange}></FormButton>
        <FormButton onClick={() => navigate('/')}></FormButton>
      </PasswordSection>
      {error && <div>: {error}</div>}
    </ProfileContainer>
  );
};

export default Profile;