import React from 'react';
import { DropdownContainer, CartItem } from './style';

const CartDropdown = () => {
  // 
  const cartItems = [
    { id: 1, name: 'Military ', color: '', size: 'F', price: 780, quantity: 1 },
    { id: 2, name: ' Chcuh ', color: '', size: 'F', price: 480, quantity: 1 },
  ];

  return (
    <DropdownContainer>
      {cartItems.length ? (
        cartItems.map(item => (
          <CartItem key={item.id}>
            <span>{item.name}</span>
            <span>{item.color} / {item.size} / {item.price} / {item.quantity}</span>
          </CartItem>
        ))
      ) : (
        <span></span>
      )}
      <button></button>
    </DropdownContainer>
  );
};

export default CartDropdown;
