import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { 
  Container, 
  Table, 
  TableHeader, 
  TableRow, 
  TableCell, 
  Title, 
  ProductTable, 
  ExpandableRow,
  NoOrderData
} from './style';
import { fetchOrders } from './orderSlice';

const Order = () => {
  const dispatch = useDispatch();
  const { orders, loading, error } = useSelector((state) => state.order);
  const [expandedOrderId, setExpandedOrderId] = useState(null);

  useEffect(() => {
    dispatch(fetchOrders());
  }, [dispatch]);

  const handleToggleExpand = (orderId) => {
    setExpandedOrderId(expandedOrderId === orderId ? null : orderId);
  };

  if (loading) {
    return (
      <Container>
        <Title>...</Title>
      </Container>
    );
  }

  if (error) {
    return (
      <Container>
        <Title>{error}</Title>
      </Container>
    );
  }

  return (
    <Container>
      <Title></Title>
      {orders.length === 0 ? (
        <NoOrderData></NoOrderData>
      ) : (
        <Table>
          <thead>
            <TableRow>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
            </TableRow>
          </thead>
          <tbody>
            {orders.map(order => (
              <React.Fragment key={order.orderId}>
                <TableRow onClick={() => handleToggleExpand(order.orderId)}>
                  <TableCell>{order.orderId}</TableCell>
                  <TableCell>
                    {order.createdAt
                      ? new Date(order.createdAt).toLocaleString('zh-TW', { timeZone: 'Asia/Taipei' })
                      : ''}
                  </TableCell>
                  <TableCell>{order.status}</TableCell>
                  <TableCell>{order.shippingMethod}</TableCell>
                  <TableCell>{order.paymentStatus}</TableCell>
                  <TableCell>{order.note}</TableCell>
                </TableRow>
                {expandedOrderId === order.orderId && (
                  <ExpandableRow>
                    <TableCell colSpan="6">
                      <ProductTable>
                        <thead>
                          <TableRow>
                            <TableHeader></TableHeader>
                            <TableHeader></TableHeader>
                            <TableHeader></TableHeader>
                            <TableHeader></TableHeader>
                            <TableHeader></TableHeader>
                          </TableRow>
                        </thead>
                        <tbody>
                          {order.items.map(item => (
                            <TableRow key={`${item.id}-${item.color}-${item.size}`}>
                              <TableCell>{item.name}</TableCell>
                              <TableCell>{`${item.color} / ${item.size}`}</TableCell>
                              <TableCell>{item.quantity}</TableCell>
                              <TableCell>{item.price} </TableCell>
                              <TableCell>{item.price * item.quantity} </TableCell>
                            </TableRow>
                          ))}
                          <TableRow>
                            <TableCell colSpan="4" style={{ textAlign: 'right', fontWeight: 'bold' }}></TableCell>
                            <TableCell>{order.totalAmount} </TableCell>
                          </TableRow>
                        </tbody>
                      </ProductTable>
                    </TableCell>
                  </ExpandableRow>
                )}
              </React.Fragment>
            ))}
          </tbody>
        </Table>
      )}
    </Container>
  );
};

export default Order;