import React, { useEffect, useState, useMemo } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
  CheckoutContainer,
  Section,
  Title,
  ReadOnlyInput,
  Input,
  SectionTitle,
  FormField,
  Label,
  StaticInfo,
  Button,
  ButtonGroup,
  ProductTable,
  TableHeader,
  TableRow,
  TableCell,
  ProductImage,
  OptionContainer,
  RadioInput,
  TextArea
} from './style';
import { submitOrder } from './checkoutSlice';
import { clearCart } from '../cart/cartSlice'; 
import { getProfile } from '../../user/profile/profileSlice';

const Checkout = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [shippingMethod, setShippingMethod] = useState(' / ');
  const [paymentMethod, setPaymentMethod] = useState('');
  const [invoiceType, setInvoiceType] = useState('');
  const [note, setNote] = useState('');

  //  Redux Store 
  const isAuthenticated = useSelector((state) => state.login.isAuthenticated);
  const cartItems = useSelector((state) => state.cart.items);

  // useState
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    address: '',
  });

  // 
  useEffect(() => {
    dispatch(getProfile())
      .unwrap()
      .then((profileData) => {
        setFormData({
          name: profileData.name || '',
          email: profileData.email || '',
          phone: profileData.phone || '',
          address: profileData.address || ''
        });
      })
      .catch((error) => {
        console.error('', error);
      });
  }, [isAuthenticated, navigate, dispatch]);

  // 
  const handleBack = () => {
    navigate('/cart');
  };

  // 
  const handleChange = (e) => {
    const { name, value } = e.target;
  
    if (['shippingMethod', 'paymentMethod', 'invoiceType', 'note'].includes(name)) {
      switch (name) {
        case 'shippingMethod':
          setShippingMethod(value);
          break;
        case 'paymentMethod':
          setPaymentMethod(value);
          break;
        case 'invoiceType':
          setInvoiceType(value);
          break;
        case 'note':
          setNote(value);
          break;
        default:
          break;
      }
    } else {
      setFormData({
        ...formData,
        [name]: value,
      });
    }
  };

  //  useMemo  cartItems 
  const totalAmount = useMemo(() => {
    return cartItems.reduce((total, item) => total + item.price * item.quantity, 0);
  }, [cartItems]);

  // 
  const handleOrderSubmit = async () => {
    const { name, email, phone, address } = formData;

    // 
    if (cartItems.length === 0) {
      alert('');
      navigate('/'); 
      return;
    }

    // 
    if (!name) {
      alert('');
      return;
    }
    if (!email) {
      alert('Email ');
      return;
    }
    if (!phone) {
      alert('');
      return;
    }
    if (!address) {
      alert('');
      return;
    }

    try {
      await dispatch(submitOrder({
        cartItems,
        user: formData,
        shippingMethod,
        paymentMethod,
        invoiceType,
        note,
        totalAmount
      })).unwrap();
      alert('');
      dispatch(clearCart());
      navigate('/order-confirmation');
    } catch (error) {
      console.error('', error);
      alert('');
    }
  };

  return (
    <CheckoutContainer>
      <Title></Title>

      {/*  */}
      <Section>
        <SectionTitle></SectionTitle>
        <FormField>
          <Label></Label>
          <Input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
          />
        </FormField>
        <FormField>
          <Label>Email</Label>
          <ReadOnlyInput
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            readOnly={!formData.email}
          />
        </FormField>
        <FormField>
          <Label></Label>
          <Input
            type="text"
            name="phone"
            value={formData.phone}
            onChange={handleChange}
          />
        </FormField>
        <FormField>
          <Label></Label>
          <Input
            type="text"
            name="address"
            value={formData.address}
            onChange={handleChange}
          />
        </FormField>
      </Section>

      {/*  */}
      <Section>
        <SectionTitle></SectionTitle>
        <ProductTable>
          <thead>
            <TableRow>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
            </TableRow>
          </thead>
          <tbody>
            {cartItems.map(item => (
              <TableRow key={`${item.id}-${item.color}-${item.size}`}>
                <TableCell>
                  <ProductImage src={item.imageUrl} alt={item.name} />
                </TableCell>
                <TableCell>{item.name}</TableCell>
                <TableCell>{`${item.color} / ${item.size}`}</TableCell>
                <TableCell>{item.quantity}</TableCell>
                <TableCell>{`${item.price} `}</TableCell>
                <TableCell>{item.price * item.quantity} </TableCell>
              </TableRow>
            ))}
          </tbody>
        </ProductTable>
        <StaticInfo style={{ textAlign: 'right', marginTop: '10px' }}>
          <strong>{totalAmount} </strong>
        </StaticInfo>
      </Section>

      {/*  */}
      <Section>
        <SectionTitle></SectionTitle>
        <OptionContainer>
          <RadioInput
            type="radio"
            name="shippingMethod"
            value=" / "
            id="shipping-post"
            checked={shippingMethod === ' / '}
            onChange={handleChange}
          />
          <Label htmlFor="shipping-post"> / </Label>

          <RadioInput
            type="radio"
            name="shippingMethod"
            value="()"
            id="shipping-convenience-store"
            checked={shippingMethod === '()'}
            onChange={handleChange}
          />
          <Label htmlFor="shipping-convenience-store">()</Label>
        </OptionContainer>
      </Section>

      {/*  */}
      <Section>
        <SectionTitle></SectionTitle>
        <OptionContainer>
          <RadioInput
            type="radio"
            name="paymentMethod"
            value=""
            id="payment-cod"
            checked={paymentMethod === ''}
            onChange={handleChange}
          />
          <Label htmlFor="payment-cod"></Label>

          <RadioInput
            type="radio"
            name="paymentMethod"
            value="()"
            id="payment-credit-card"
            checked={paymentMethod === '()'}
            onChange={handleChange}
          />
          <Label htmlFor="payment-credit-card">()</Label>
        </OptionContainer>
      </Section>

      {/*  */}
      <Section>
        <SectionTitle></SectionTitle>
        <OptionContainer>
          <RadioInput
            type="radio"
            name="invoiceType"
            value=""
            checked={invoiceType === ''}
            onChange={handleChange}
          />
          <Label></Label>
        </OptionContainer>
        <OptionContainer>
          <RadioInput
            type="radio"
            name="invoiceType"
            value=""
            checked={invoiceType === ''}
            onChange={handleChange}
          />
          <Label></Label>
        </OptionContainer>
      </Section>

      {/*  */}
      <Section>
        <SectionTitle></SectionTitle>
        <FormField>
          <Label></Label>
          <TextArea
            name="note"
            value={note}
            onChange={handleChange}
            placeholder=""
          />
        </FormField>
      </Section>

      {/*  */}
      <ButtonGroup>
        <Button onClick={handleBack}></Button>
        <Button onClick={handleOrderSubmit}></Button>
      </ButtonGroup>
    </CheckoutContainer>
  );
};

export default Checkout;