import { notifyUser } from './notification';

export const handleError = (type, error) => {
  // 
  if (process.env.NODE_ENV === 'development') {
    console.error(`[${type} Error]:`, error);
  }

  switch (type) {
    case 'auth':
      // 
      notifyUser('');
      break;

    case 'api':
      //  API 
      if (error.response && error.response.status === 404) {
        notifyUser('');
      } else if (error.response && error.response.status === 500) {
        notifyUser('');
      } else {
        notifyUser('');
      }
      break;

    case 'form':
      // 
      notifyUser('');
      break;

    case 'validation':
      // 
      notifyUser('');
      break;

    case 'navigation':
      // 
      notifyUser('');
      break;

    case 'stock':
      // 
      notifyUser('');
      break;

    case 'image':
      // 
      notifyUser('');
      break;

    default:
      // 
      notifyUser('');
  }
};