import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { resetPassword, setPasswordValid, setPasswordError, setConfirmPasswordValid, setConfirmPasswordError } from '../../forgotPasswordSlice';
import {
  ForgotPasswordContainer,
  ForgotPasswordTitle,
  ForgotPasswordBox,
  Label,
  Input,
  Button,
  ErrorText
} from '../../style';

const ResetPassword = () => {
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { passwordValid, confirmPasswordValid, passwordError, confirmPasswordError, loading } = useSelector((state) => state.forgotPassword);

  //  "" 
  const validatePassword = (password) => {
    let errorMessage = '';
    const re = /^[A-Z].{7,}$/; // 8
    const hasUpperCaseFirst = /^[A-Z]/.test(password); // 
    const isValidLength = password.length >= 8;

    if (!hasUpperCaseFirst) {
      errorMessage = '';
    } else if (!isValidLength) {
      errorMessage = '8';
    }

    const isValid = re.test(password);
    dispatch(setPasswordValid(isValid));
    dispatch(setPasswordError(isValid ? '' : errorMessage));
  };

  //  "" 
  const validateConfirmPassword = (password, confirmPassword) => {
    const isValid = confirmPassword === password && passwordValid;
    dispatch(setConfirmPasswordValid(isValid));
    dispatch(setConfirmPasswordError(isValid ? '' : ''));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // 
    if (!passwordValid) {
      setError('');
      return;
    }

    // 
    if (!confirmPasswordValid) {
      setError('');
      return;
    }

    const oobCode = new URLSearchParams(window.location.search).get('oobCode'); //  URL  OOB Code
    if (!oobCode) {
      setError('');
      return;
    }

    // 
    dispatch(resetPassword({ oobCode, password }))
      .unwrap()
      .then(() => {
        window.alert('');
        navigate('/login');
      })
      .catch((err) => setError(err));
  };

  return (
    <ForgotPasswordContainer>
      <ForgotPasswordTitle></ForgotPasswordTitle>
      <ForgotPasswordBox>
        <form onSubmit={handleSubmit}>
          <Label></Label>
          <Input
            type="password" 
            placeholder="New Password" 
            value={password} 
            onChange={(e) => {
              setPassword(e.target.value);
              validatePassword(e.target.value);
            }}
            required 
          />
          {passwordError && <ErrorText>{passwordError}</ErrorText>}
          <Label></Label>
          <Input 
            type="password" 
            placeholder="Confirm New Password" 
            value={confirmPassword} 
            onChange={(e) => {
              setConfirmPassword(e.target.value);
              validateConfirmPassword(password, e.target.value);
            }}
            required 
          />
          {confirmPasswordError && <ErrorText>{confirmPasswordError}</ErrorText>}
          {error && <ErrorText>{error}</ErrorText>}
          <Button type="submit" disabled={loading}></Button>
        </form>
      </ForgotPasswordBox>
    </ForgotPasswordContainer>
  );
};

export default ResetPassword;