import React, { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { 
  setEmail, 
  setPassword, 
  setEmailValid, 
  setPasswordValid, 
  setEmailError, 
  setPasswordError, 
  setError, 
  resetForm, 
  login
} from './loginSlice';
import { 
  LoginContainer,
  LoginTitle,
  LoginBox,
  Label,
  Input,
  Button,
  HelperText,
  Link,
  SuccessText,
  ErrorText
} from './style.js';

const Login = () => {
  const dispatch = useDispatch(); 
  const navigate = useNavigate();
  const location = useLocation();
  const { from } = location.state || { from: { pathname: "/" } };

  const {
    email,
    password,
    emailValid,
    passwordValid,
    emailError,
    passwordError,
    error,
    success,
    loading,
    isAuthenticated
  } = useSelector((state) => state.login);

  // 
  useEffect(() => {
    return () => {
      dispatch(resetForm());
    };
  }, [dispatch]);

  // 
  const validateEmail = (email) => {
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const isValidFormat = re.test(email);
    if (isValidFormat) {
      dispatch(setEmailValid(true));
      dispatch(setEmailError(''));
    } else {
      dispatch(setEmailValid(false));
      dispatch(setEmailError(''));
    }
  };

  // 
  const validatePassword = (password) => {
    let errorMessage = '';
    const re = /^[A-Z].{7,}$/; // 8
    const hasUpperCaseFirst = /^[A-Z]/.test(password); // 
    const isValidLength = password.length >= 8;

    if (!hasUpperCaseFirst) {
      errorMessage = '';
    } else if (!isValidLength) {
      errorMessage = '8';
    }

    const isValid = re.test(password);
    dispatch(setPasswordValid(isValid));
    dispatch(setPasswordError(isValid ? '' : errorMessage));
  };

  // 
  const handleLogin = async (e) => {
    e.preventDefault(); // 

    // 
    if (!email) {
      dispatch(setError(''));
    } else if (!password) {
      dispatch(setError(''));
    } else if (!emailValid && !passwordValid) {
      dispatch(setError(''));
    } else if (!emailValid) {
      dispatch(setError(''));
    } else if (!passwordValid) {
      dispatch(setError(''));
    } else {
      //  Redux Thunk 
      dispatch(login({ email, password }))
        .unwrap()
        .then(() => {
          dispatch(resetForm());
          navigate(from); // 
        })
        .catch((errorMessage) => {
          console.error(': ', errorMessage);
          dispatch(setError(errorMessage));
          // 3
          setTimeout(() => {
            dispatch(setError(''));
            dispatch(resetForm());
          }, 3000);
        });
    }
  };

  //  handleLogin 
  useEffect(() => {
    if (isAuthenticated) {
      navigate(from);
    }
  }, [isAuthenticated, navigate, from]);

  return (
    <LoginContainer>
      <LoginTitle></LoginTitle> 
      <LoginBox>
        <form onSubmit={handleLogin} noValidate>
          <Label>:</Label>
          <Input 
            type="email" 
            placeholder="Email" 
            value={email} 
            onChange={(e) => {
              dispatch(setEmail(e.target.value));
              validateEmail(e.target.value);
            }} 
            required 
          />
          {email && (emailValid ? <SuccessText></SuccessText> : <ErrorText> {emailError}</ErrorText>)}
          <Label>:</Label>
          <Input 
            type="password" 
            placeholder="Password" 
            value={password} 
            onChange={(e) => {
              dispatch(setPassword(e.target.value));
              validatePassword(e.target.value);
            }} 
            required 
          />
          {password && (passwordValid ? <SuccessText></SuccessText> : <ErrorText> {passwordError}</ErrorText>)}
          <Button type="submit" disabled={loading}></Button>
          {error && <ErrorText>{error}</ErrorText>}
          {success && <SuccessText>{success}</SuccessText>}
          <HelperText>
            <Link onClick={() => navigate('/forgotpassword/email')}>?</Link> / <Link onClick={() => navigate('/Register')}></Link>
          </HelperText>
        </form>
      </LoginBox>
    </LoginContainer>
  );
};

export default Login;