import React, { useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { auth } from '../../../config/firebaseConfig';
import CartItem from './components/CartItem';

import {
  CartContainer,
  Title,
  Table,
  TableHeader,
  TableRow,
  SummaryContainer,
  Button,
  Section,
} from './style';

import {
  fetchCart,
  removeItem,
  removeItemFromCart,
  updateQuantity,
  updateItemQuantity,
  setCartItems,
  syncCartToFirestore,
} from './cartSlice';

const Cart = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { items: cartItems } = useSelector((state) => state.cart);
  const isAuthenticated = useSelector((state) => state.login.isAuthenticated);

  //  useMemo 
  const total = React.useMemo(() => {
    return cartItems.reduce((acc, item) => acc + item.price * item.quantity, 0);
  }, [cartItems]);

  // 
  const mergeLocalAndRemoteCart = useCallback(async () => {
    const localItems = JSON.parse(localStorage.getItem('cartItems')) || [];

    if (isAuthenticated && auth.currentUser) {
      try {
        const remoteItems = await dispatch(fetchCart()).unwrap();
        const mergedItems = [...remoteItems];

        localItems.forEach((localItem) => {
          const existingItem = mergedItems.find(
            (item) =>
              item.id === localItem.id &&
              item.color === localItem.color &&
              item.size === localItem.size
          );
          if (existingItem) {
            existingItem.quantity += localItem.quantity;
          } else {
            mergedItems.push(localItem);
          }
        });

        //  Redux 
        dispatch(setCartItems(mergedItems));

        // 
        localStorage.removeItem('cartItems');

        //  Firestore
        await dispatch(syncCartToFirestore()).unwrap();
      } catch (error) {
        console.error('', error);
      }
    } else {
      // 
      dispatch(setCartItems(localItems));
    }
  }, [isAuthenticated, dispatch]);

  useEffect(() => {
    const interval = setInterval(() => {
      if (auth.currentUser && isAuthenticated) {
        mergeLocalAndRemoteCart();
        clearInterval(interval); // 
      }
    }, 100); // 100
    
    return () => clearInterval(interval); // 
  }, [isAuthenticated, mergeLocalAndRemoteCart]);
  
  // 
  const handleRemove = (id, color, size) => {
    dispatch(removeItem({ id, color, size }));
    dispatch(removeItemFromCart({ id, color, size }))
      .unwrap()
      .catch((error) => {
        console.error('', error);
        alert('');
      });
  };

  // 
  const handleQuantityChange = (id, color, size, quantity) => {
    if (quantity < 1 || quantity > 20 || isNaN(quantity)) {
      alert(' 1  20 ');
      return;
    }
    dispatch(updateQuantity({ id, color, size, quantity }));
    dispatch(updateItemQuantity({ id, color, size, quantity }))
      .unwrap()
      .catch((error) => {
        console.error('', error);
        alert('');
      });
  };

  // 
  const handleCheckout = () => {
    if (!isAuthenticated) {
      alert('');
      return;
    }
    navigate('/checkout');
  };

  return (
    <CartContainer>
      <Title></Title>
      <Table>
        <thead>
          <TableRow>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
          </TableRow>
        </thead>
        { /* CartItem */}
        <tbody>
          {cartItems.map((item) => (
            <CartItem
            key={`${item.id}-${item.color}-${item.size}`}
            item={item}
            onQuantityChange={handleQuantityChange}
            onRemove={handleRemove}
            />
          ))}
        </tbody>
      </Table>
      <SummaryContainer>
        <span></span>
        <span>{total} </span>
      </SummaryContainer>
      <Section>
        <Button onClick={() => navigate('/')}></Button>
        <Button onClick={handleCheckout}></Button>
      </Section>
    </CartContainer>
  );
};

export default Cart;