import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { sendResetEmail, setEmail, setEmailValid, setEmailError, setError, resetForm } from '../../forgotPasswordSlice';
import { fetchSignInMethodsForEmail } from 'firebase/auth';
import { auth } from '../../../../../../config/firebaseConfig';
import {
  ForgotPasswordContainer,
  ForgotPasswordTitle,
  ForgotPasswordBox,
  Label,
  Input,
  Button,
  ErrorText
} from '../../style';

const EmailInput = () => {
  const dispatch = useDispatch();
  const { email, emailError, error, loading } = useSelector((state) => state.forgotPassword); //  emailValid

  // 
  const checkEmailRegistered = async (email) => {
    try {
      const signInMethods = await fetchSignInMethodsForEmail(auth, email); //  Firebase 
      if (signInMethods.length > 0) {
        dispatch(setEmailValid(true));
        dispatch(setEmailError(''));
        return true;
      } else {
        dispatch(setEmailValid(false));
        dispatch(setEmailError(''));
        return false;
      }
    } catch (error) {
      dispatch(setEmailError(''));
      dispatch(setEmailValid(false));
      return false;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
  
    try {
      // 
      const isEmailRegistered = await checkEmailRegistered(email);
  
      if (isEmailRegistered) {
        // 
        await dispatch(sendResetEmail(email)).unwrap();
        window.alert('');
      } else {
        // 
        window.alert('');
        dispatch(resetForm());
      }
    } catch (err) {
      // 
      console.error(':', err);
      window.alert(': ' + (err.message || err));
      dispatch(setError(err.message || ''));
    }
  };
  

  return (
    <ForgotPasswordContainer>
      <ForgotPasswordTitle></ForgotPasswordTitle>
      <ForgotPasswordBox>
        <form onSubmit={handleSubmit}>
          <Label></Label>
          <Input 
            type="email" 
            placeholder="Email" 
            value={email} 
            onChange={(e) => dispatch(setEmail(e.target.value))} 
            required 
          />
          {emailError && <ErrorText>{emailError}</ErrorText>}
          {error && <ErrorText>{error}</ErrorText>}
          <Button type="submit" disabled={loading}></Button>
        </form>
      </ForgotPasswordBox>
    </ForgotPasswordContainer>
  );
};

export default EmailInput;