import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
  ConfirmationContainer,
  Title,
  InfoBlock,
  InfoTitle,
  DataRow,
  DataLabel,
  DataValue,
  Button,
  ButtonGroup,
  SectionTitle,
  ProductTable,
  TableRow,
  TableHeader,
  TableCell,
  ProductImage,
} from './style';
import { clearOrderStatus } from '../checkout/checkoutSlice';

const OrderConfirmation = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const orderDetails = useSelector((state) => state.checkout.orderDetails);

  useEffect(() => {
    if (!orderDetails) {
      navigate('/');
    }
  }, [orderDetails, navigate]);

  if (!orderDetails) {
    return null;
  }

  const handleGoHome = () => {
    dispatch(clearOrderStatus());
    navigate('/');
  };

  return (
    <ConfirmationContainer>
      <Title></Title>
      <InfoBlock>
        <InfoTitle></InfoTitle>
        <DataRow>
          <DataLabel></DataLabel>
          <DataValue>{orderDetails.orderId}</DataValue>
        </DataRow>
        <DataRow>
          <DataLabel></DataLabel>
          <DataValue>{orderDetails.totalAmount} </DataValue>
        </DataRow>
        <DataRow>
          <DataLabel></DataLabel>
          <DataValue>{orderDetails.paymentMethod}</DataValue>
        </DataRow>
        <DataRow>
          <DataLabel></DataLabel>
          <DataValue>{orderDetails.paymentStatus}</DataValue>
        </DataRow>
      </InfoBlock>
      <InfoBlock>
        <InfoTitle></InfoTitle>
        <DataRow>
          <DataLabel></DataLabel>
          <DataValue>{orderDetails.userInfo.name}</DataValue>
        </DataRow>
        <DataRow>
          <DataLabel></DataLabel>
          <DataValue>{orderDetails.userInfo.address}</DataValue>
        </DataRow>
        <DataRow>
          <DataLabel></DataLabel>
          <DataValue>{orderDetails.userInfo.phone}</DataValue>
        </DataRow>
        <DataRow>
          <DataLabel>Email</DataLabel>
          <DataValue>{orderDetails.userInfo.email}</DataValue>
        </DataRow>
      </InfoBlock>
      <InfoBlock>
        <SectionTitle></SectionTitle>
        <ProductTable>
          <thead>
            <TableRow>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
              <TableHeader></TableHeader>
            </TableRow>
          </thead>
          <tbody>
            {orderDetails.items.map((item) => (
              <TableRow key={`${item.id}-${item.color}-${item.size}`}>
                <TableCell>
                  <ProductImage src={item.imageUrl} alt={item.name} />
                </TableCell>
                <TableCell>{item.name}</TableCell>
                <TableCell>{`${item.color} / ${item.size}`}</TableCell>
                <TableCell>{item.quantity}</TableCell>
                <TableCell>{`${item.price} `}</TableCell>
                <TableCell>{item.price * item.quantity} </TableCell>
              </TableRow>
            ))}
          </tbody>
        </ProductTable>
      </InfoBlock>
      <ButtonGroup>
        <Button onClick={handleGoHome}></Button>
      </ButtonGroup>
    </ConfirmationContainer>
  );
};

export default OrderConfirmation;