import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
  register,
  setEmail,
  setPassword,
  setConfirmPassword,
  setEmailValid,
  setPasswordValid,
  setConfirmPasswordValid,
  setEmailError,
  setPasswordError,
  setConfirmPasswordError,
  setError,
  setSuccess,
  resetForm
} from './registerSlice';
import {
  RegisterContainer,
  RegisterTitle,
  RegisterBox,
  Label,
  Input,
  Button,
  HelperText,
  Link,
  SuccessText,
  ErrorText
} from './style.js';

const Register = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const {
    email,
    password,
    confirmPassword,
    emailValid,
    passwordValid,
    confirmPasswordValid,
    emailError,
    passwordError,
    confirmPasswordError,
    error,
    success
  } = useSelector((state) => state.register);

  // 
  const validateEmail = (email) => {
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const isValidFormat = re.test(email);
    dispatch(setEmailValid(isValidFormat));
    dispatch(setEmailError(isValidFormat ? '' : ''));
  };

  // 
  const validatePassword = (password) => {
    let errorMessage = '';
    const re = /^[A-Z].{7,}$/; // 8
    const hasUpperCaseFirst = /^[A-Z]/.test(password); // 
    const isValidLength = password.length >= 8;

    if (!hasUpperCaseFirst) {
      errorMessage = '';
    } else if (!isValidLength) {
      errorMessage = '8';
    }

    const isValid = re.test(password);
    dispatch(setPasswordValid(isValid));
    dispatch(setPasswordError(isValid ? '' : errorMessage));
  };

  // 
  const validateConfirmPassword = (password, confirmPassword, passwordValid) => {
    const isValid = confirmPassword === password && passwordValid;
    dispatch(setConfirmPasswordValid(isValid));
    dispatch(setConfirmPasswordError(isValid ? '' : ''));
  };

  // 
  const handleRegister = async (e) => {
    e.preventDefault(); // 
  
    // 
    if (!emailValid) {
      dispatch(setError(''));
    } else if (!passwordValid) {
      dispatch(setError(''));
    } else if (!confirmPasswordValid) {
      dispatch(setError(''));
    } else {
      //  Redux Thunk 
      dispatch(register({ email, password }))
        .unwrap()
        .then(() => {
          dispatch(setSuccess(''));
          // 
          setTimeout(() => {
            navigate('/'); // 
            dispatch(setSuccess(''));
            dispatch(resetForm());
          }, 500); // 
        })
        .catch((error) => {
          console.error(': ', error);
          dispatch(setError(''));
          // 3
          setTimeout(() => {
            dispatch(setError(''));
            dispatch(resetForm());
          }, 3000);
        });
    }
  };

  // 
  useEffect(() => {
    return () => {
      dispatch(resetForm());
    };
  }, [dispatch]);

  return (
    <RegisterContainer>
      <RegisterTitle></RegisterTitle>
      <RegisterBox>
        <form onSubmit={handleRegister} noValidate>
          <Label>:</Label>
          <Input
            type="email"
            placeholder="Email"
            value={email}
            onChange={(e) => {
              dispatch(setEmail(e.target.value));
              validateEmail(e.target.value);
            }}
          />
          {email && (emailValid ? <SuccessText></SuccessText> : <ErrorText> {emailError}</ErrorText>)}
          <Label>:</Label>
          <Input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => {
              dispatch(setPassword(e.target.value));
              validatePassword(e.target.value);
            }}
          />
          {password && (passwordValid ? <SuccessText></SuccessText> : <ErrorText> {passwordError}</ErrorText>)}
          <Label>:</Label>
          <Input
            type="password"
            placeholder="Confirm Password"
            value={confirmPassword}
            onChange={(e) => {
              dispatch(setConfirmPassword(e.target.value));
              validateConfirmPassword(password, e.target.value, passwordValid);
            }}
          />
          {confirmPassword && (confirmPasswordValid ? <SuccessText></SuccessText> : <ErrorText> {confirmPasswordError}</ErrorText>)}
          <Button type="submit"></Button>
          {error && <ErrorText>{error}</ErrorText>}
          {success && <SuccessText>{success}</SuccessText>}
          <HelperText>
            <Link onClick={() => navigate('/Login')}></Link>
          </HelperText>
        </form>
      </RegisterBox>
    </RegisterContainer>
  );
};

export default Register;