import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { deleteUserAccount } from './deleteAccountSlice';
import {
  DeleteAccountContainer,
  DeleteAccountTitle,
  DeleteAccountBox,
  Label,
  Input,
  Button,
  ErrorText
} from './style';

const DeleteAccount = () => {
  const [password, setPassword] = useState('');
  const [confirmationText, setConfirmationText] = useState('');
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { error } = useSelector((state) => state.deleteAccount);

  // 
  const handleAccountDelete = (e) => {
    e.preventDefault();

    // 
    if (confirmationText !== '') {
      return dispatch({ type: 'deleteUser/setError', payload: ' "" ' });
    }

    //  Redux Action
    dispatch(deleteUserAccount({ password, confirmationText }))
      .then(() => {
        window.alert('');
        navigate('/');
      })
      .catch((err) => {
        console.error('Error while deleting account:', err);
        window.alert('');
      });
  };

  return (
    <DeleteAccountContainer>
      <DeleteAccountTitle></DeleteAccountTitle>
      <DeleteAccountBox>
        <form onSubmit={handleAccountDelete}>
          <Label htmlFor="password"></Label>
          <Input
            id="password"
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            autoComplete="current-password"
            required
          />

          <Label htmlFor="confirmationText"> "" </Label>
          <Input
            id="confirmationText"
            type="text"
            placeholder=""
            value={confirmationText}
            onChange={(e) => setConfirmationText(e.target.value)}
            required
          />

          {error && <ErrorText>{error}</ErrorText>}
          <Button type="submit" disabled={password === '' || confirmationText !== ''}>
            
          </Button>
        </form>
      </DeleteAccountBox>
    </DeleteAccountContainer>
  );
};

export default DeleteAccount;